.shop {
  width: 60%;
  margin: 0 auto;
  border: 1px solid black;
  padding: 5px;
}

h1 {
  text-align: center;
  color: white;
  background-color: orangered;
  padding: 5px 0px;
}


.card {
  border: 1px solid black;
  margin: 0px 10px 10px 10px;
  padding: 0px 10px 10px 10px;
}

.title {
  color: green;
  font-weight: bold;
  font-size: 20px;
}

.vitrine {
  display: grid;
  grid-template-columns: 1fr 1fr;
}



